import { Component, Host, h, State, Listen, Prop } from '@stencil/core';
import { IconName } from '../../atoms/icon/icon.types';
import { throttle } from '../../../utils/throttle';

/**
 * The Slider component.
 */
@Component({
  tag: 'bs-slider',
  styleUrl: 'slider.scss',
  shadow: true,
})
export class BsSlider {
  private teaserSliderRef: HTMLDivElement;

  private navigationRef: HTMLDivElement;

  /** States */
  @State() slides: boolean = true;

  @State() activeIndex: number = 0;

  /** Props */
  @Prop() navigationOverlay: boolean = false;

  /** listeners */
  @Listen('resize', { target: 'window' })
  handleScreenResized() {
    this.throttleMethod();
  }

  /** Methods */
  private onResize = () => {
    this.slides =
      this.teaserSliderRef && this.teaserSliderRef.scrollWidth > this.teaserSliderRef.offsetWidth;
  };

  private throttleMethod = throttle(this.onResize, 1000);

  private onPreviousSlide = () => {
    if (this.teaserSliderRef.scrollLeft === 0) return;
    this.activeIndex -= 1;

    this.scrollSlider();
  };

  private onNextSlide = () => {
    if (
      Math.abs(this.teaserSliderRef.scrollLeft) + this.teaserSliderRef.clientWidth ===
      this.teaserSliderRef.scrollWidth
    )
      return;
    this.activeIndex += 1;

    this.scrollSlider();
  };

  private scrollSlider = () => {
    let scrollDistance;
    const activeSliderItem = (
      this.teaserSliderRef.children[0] as HTMLSlotElement
    ).assignedElements()[this.activeIndex] as HTMLElement;

    if (document.dir === 'rtl') {
      scrollDistance =
        activeSliderItem.offsetLeft +
        activeSliderItem.offsetWidth -
        (this.teaserSliderRef.offsetLeft + this.teaserSliderRef.offsetWidth);
    } else {
      scrollDistance = activeSliderItem.offsetLeft - this.teaserSliderRef.offsetLeft;
    }

    this.teaserSliderRef.scrollTo({
      top: 0,
      left: scrollDistance,
      behavior: 'smooth',
    });
  };

  /** lifecycle hooks */
  componentDidLoad() {
    this.slides = this.teaserSliderRef.scrollWidth > this.teaserSliderRef.offsetWidth;

    if (this.slides) {
      this.teaserSliderRef.addEventListener(
        'scroll',
        throttle(() => {
          if (
            Math.abs(this.teaserSliderRef.scrollLeft) + this.teaserSliderRef.offsetWidth >=
            this.teaserSliderRef.scrollWidth
          ) {
            this.navigationRef.classList.add('navigation--transparent');
          } else {
            this.navigationRef.classList.remove('navigation--transparent');
          }
        }, 0)
      );
    }
  }

  render() {
    return (
      <Host>
        {this.slides && (
          <div class={this.navigationOverlay ? 'overlay' : 'head'}>
            <div
              class={`navigation ${this.navigationOverlay ? 'navigation--overlay' : ''}`}
              ref={(el) => {
                this.navigationRef = el;
              }}
            >
              <div class="navigation-back">
                <bs-click-action
                  name="previous"
                  onBsClickAction={this.onPreviousSlide}
                  tagClass="navigation-button"
                  ariaLabelText="backward"
                >
                  <bs-icon name={IconName.ArrowBackward} />
                </bs-click-action>
              </div>
              <div class="navigation-forward">
                <bs-click-action
                  name="next"
                  onBsClickAction={this.onNextSlide}
                  tagClass="navigation-button"
                  ariaLabelText="forward"
                >
                  <bs-icon name={IconName.ArrowForward} />
                </bs-click-action>
              </div>
            </div>
          </div>
        )}
        <div
          class="teaser-slider"
          ref={(el) => {
            this.teaserSliderRef = el;
          }}
        >
          <slot name="slider-item" />
        </div>
      </Host>
    );
  }
}
