/* stylelint-disable property-no-vendor-prefix */
:host {
  @include reset-host;
  position: relative;
}

:host .overlay {
  @include z-index('base');
  @include cover;
  display: flex;
  height: calc(100% - rem(20));
  pointer-events: none;

  @include mq('max', 'md') {
    display: none;
  }
}

:host .navigation {
  position: relative;
  width: 100%;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0) 80%,
    rgba(255, 255, 255, 0.8) 90%,
    rgba(255, 255, 255, 1) 100%
  );

  &--transparent {
    background: transparent;
  }
}

:host .navigation-back {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-50%, -50%);
}

:host .navigation-forward {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(50%, -50%);
}

:host .head {
  position: absolute;
  top: rem(-32);
  right: 0;
  display: flex;
  width: rem(96);
  height: rem(42);
  pointer-events: none;
  transform: translateY(rem(-42));

  .navigation-back,
  .navigation-forward {
    transform: translateY(-50%);
  }
}

:host .teaser-slider {
  display: flex;
  gap: rem(12);
  overflow: auto;

  @include mq('min', 'md') {
    gap: rem(24);
    padding-bottom: rem(20);
  }

  &::-webkit-scrollbar-track {
    background-color: $gray-900;
  }

  &::-webkit-scrollbar {
    height: 2px;
    background-color: transparent;

    @include mq('max', 'md') {
      display: none;
    }
  }

  &::-webkit-scrollbar-thumb {
    background-color: $primary;
  }
}

:host .navigation-button {
  padding: rem(8);
  font-size: rem(24);
  pointer-events: all;
  background-color: $white;
  border: 1px solid $gray-800;
  border-radius: 50%;

  &:focus {
    background-color: $gray-800;
    outline: 0;
  }
}

:host-context(html[dir='rtl']) {
  .navigation {
    transform: rotateY(180deg);
  }

  .navigation-button {
    transform: rotateY(180deg);
  }

  .head {
    right: unset;
    left: 0;
  }
}
